@import "../../global.scss";

.portfolio {
  background-color: rgb(67, 0, 80);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;

  h1 {
    font-size: 50px;

    @include mobile {
      font-size: 20px;
    }
  }

  ul {
    margin: 20px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;

    @include mobile {
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @include mobile {
      width: 100%;
    }

    .item2 {
      text-align: center;
      width: 375px;
      height: 275px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

      @include mobile {
        width: 150px;
        height: 150px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
        color: white;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }
      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
