@import "../../global.scss";

.contact {
    background-color: rgb(255, 255, 255);
    display: flex;

    @include mobile {
        flex-direction: column;
       
     }

    .left {
        flex: 1.25;
        overflow: hidden;

        img {
            height: 90%;
            left: 0;
            @include mobile {
                width: 100%;
               
             }
        }
    }
    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2 {
            font-size: 30px;
        }

        form {
            width: 70%;
            height: 60%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input {
                width: 300px;
                height: 30px;
                font-size: 14px;
                @include mobile {
                    width: 200px;
                    height: 20px;
                   
                 }
            }

            textarea {
                width: 300px;
                height: 200px;
                font-size: 14px;
                @include mobile {
                    width: 200px;
                    height: 100px;
                   
                 }
            }

            button {
                width: 150px;
                height: 30px;
                color: white;
                background-color: rgb(207, 81, 50);
                border: none;
                border-radius: 15px;
                font-weight: 500;
                cursor: pointer;

                &:focus {
                    outline: none;
                }
            }
            span{
                color:green
            }
        }
    }
}
