@import "../../global.scss";

.works {
    background-color: rgb(168, 209, 216);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 60px;
        position: absolute;
        cursor: pointer;

        @include mobile {
            display: none;
        }


        &.left{
            left: 100px;
            transform: rotateY(180deg);
        }

        &.right{
            right: 100px;
            
        }
    }

    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile {
           height: 100vh;
           flex-direction: column;
           justify-content: center;
        }

        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item {
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 30px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile {
                    width: 90%;
                    height: 200px;
                    margin: 5px 0;
                 }

                .left {
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer {
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer {
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: coral;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img {
                                width: 25px;
                            }

                            @include mobile {
                                width: 35px;
                                height: 100px;
                            }
                        }
                        h2 {
                            font-size: 20px;

                            @include mobile {
                                font-size: 18px;
                                height: 150px;
                                flex-wrap: wrap;
                             }
                        }
                        p {
                            font-size: 13px;
                            @include mobile {
                                font-size: 10px;
                                height: 150px;
                                flex-wrap: wrap;
                             }
                        }
                        span {
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                            @include mobile {
                                display: none;
                            }
                        }
                    }
                }

                .right {
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    img{
                        width: 400px;
                        transform: rotate(-10deg);

                        @include mobile {
                            width: 100%;
                         }
                    }
                }
            }
        }
    }
}
