@import "../../global.scss";

.topbar {
    margin: 0;
    margin-top: 0;
    background-color: rgb(67, 0, 80);
    color: white;
    width: 100%;
    height: 70px;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease ;

    a {
        color: white;
        text-decoration: none;
        font-weight: 700;

    }

    .osLink {
        padding-left: 20px;
        transition: .25s;
        text-decoration: none;
    }
    .osLink:hover {
        padding-left: 20px;
        font-size: 24px;
        color: rgb(0, 255, 255);
    }
    .mintLink {
        padding-left: 20px;
        transition: .25s;
        text-decoration: none;
    }
    .mintLink:hover {
        padding-left: 20px;
        font-size: 24px;
        color: rgb(60, 255, 0);
    }

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 40px;
                font-weight: 70px;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile{
                    display: none;
                }

                .icon {
                    font-size: 20px;
                    margin-right: 5px;
                }
                .span {
                    font-size: 15px;
                    font-weight: 700;
                }
            }
        }

        .right{
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                 
                span{
                    width: 100%;
                    height: 1px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active{
        background-color: $mainColor;
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}
